import DateRange, { modes } from "../date-range";
import Dropdown, { types } from "../dropdown";
import initSlider from "../range-slider";
import ListOfCheckboxes from "../list-of-checkboxes";

import "./style.scss";

export default function searchRoomFormInit() {
  const dateRange = new DateRange({
    classId: "js-date-range-room-selector",
    mode: modes.single,
  });

  dateRange.start();

  const guestsDropdown = new Dropdown({
    classId: "js-dropdown-room-finder-guests",
    type: types.guests,
    defaultMessage: "Сколько гостей",
  });
  guestsDropdown.start();

  const roomDropdown = new Dropdown({
    classId: "js-dropdown-room-finder-rooms",
    type: types.rooms,
    defaultMessage: "Сколько комнат",
  });
  roomDropdown.start();

  initSlider({
    classId: "js-range-slider-search-room-form",
    minValue: 1000,
    maxValue: 20000,
    startValue: 5000,
    endValue: 10000,
  });

  const expandable = new ListOfCheckboxes("js-expandable-checkbox-list-finder");
  expandable.toggleContent();
}
