import "../checkbox";

import "./style.scss";

export default class ListOfCheckboxes {
  constructor(classId) {
    this.classId = classId;
    this.expandableEl = document.querySelector(`.${classId}`);
    this.headerClass = "js-list-of-checkboxes__header";
    this.headerEl = document.querySelector(`.${classId} .${this.headerClass}`);

    this.openClass = "list-of-checkboxes_close";

    if (this.expandableEl === null) throw new Error(`list of checkboxes with id ${classId} not found`);
    if (this.headerEl === null) throw new Error(`list of ckeckboxes header with id ${classId} not found`);
  }

  start() {
    this._initEvents();
  }

  toggleContent() {
    if (this.expandableEl.classList.contains(this.openClass)) {
      this.expandableEl.classList.remove(this.openClass);
      return;
    }

    this.expandableEl.classList.add(this.openClass);
  }

  _initEvents() {
    this.headerEl.addEventListener("mousedown", this._mousedownHeaderHandler.bind(this));
    this.headerEl.addEventListener("keydown", this._keydownHeaderHandler.bind(this));
  }

  _keydownHeaderHandler(e) {
    if (e.code === "Space" || e.code === "Enter") {
      e.preventDefault();
      e.stopPropagation();

      this.toggleContent();
    }
  }

  _mousedownHeaderHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this.toggleContent();
  }
}
