import "./style.scss";
import "nouislider/distribute/nouislider.min.css";
import noUiSlider from "nouislider";

function _priceFormat(price) {
  return `${Math.floor(price)
    .toString()
    .split("")
    .reverse()
    .map((ch, index) => ((index + 1) % 3 === 0 ? ` ${ch}` : ch))
    .reverse()
    .join("")}₽`;
}

export default function initSlider({
  classId, minValue, maxValue, step, startValue, endValue,
}) {
  if (maxValue === undefined || minValue === undefined) {
    throw new Error("not specified maxValue or minValue");
  }

  const sliderEl = document.querySelector(`.${classId} .js-slider__inner`);

  const valueLowerEl = document.querySelector(`.${classId} .js-slider__value-lower`);
  const valueUpperEl = document.querySelector(`.${classId} .js-slider__value-upper`);

  const _minValue = minValue;
  const _maxValue = maxValue;
  const _startValue = startValue || _minValue;
  const _endValue = endValue || _maxValue;

  const slider = noUiSlider.create(sliderEl, {
    start: [_startValue, _endValue],
    connect: true,
    step: step || 100,
    range: {
      min: _minValue,
      max: _maxValue,
    },
  });

  const sliderHandler = ([low, hight]) => {
    valueLowerEl.innerHTML = _priceFormat(low);
    valueUpperEl.innerHTML = _priceFormat(hight);
  };

  sliderHandler([_minValue, _maxValue]);

  slider.on("slide", sliderHandler);
}
